  // Removed keyboard shortcuts for timer control
  

  // Update player stat
  const updatePlayerStat = (team, playerIndex, statType, value) => {
    const newValue = Math.max(0, parseInt(value) || 0);
    setPlayerStats(prev => {
      const updatedStats = {
        ...prev,
        [team]: prev[team].map((player, index) => 
          index === playerIndex 
            ? { ...player, [statType]: newValue }
            : player
        )
      };
      
      // Get the player name for the event
      const playerName = prev[team][playerIndex]?.name || '';
      
      //  IMMEDIATE UPDATE - Dispatch stat change event
      window.dispatchEvent(new CustomEvent('mrvl-match-updated', {
        detail: {
          matchId: matchStats.matchId,
          updateType: 'STAT_UPDATE',
          timestamp: Date.now(),
          mapIndex: matchStats.currentMapIndex,
          team: team,
          playerIndex: playerIndex,
          playerName: playerName,
          statType: statType,
          statName: statType,
          value: newValue,
          statValue: newValue
        }
      }));

      //  SSE UPDATE - Send stats change to backend
      sendLiveUpdate(matchStats.matchId, 'stats-update', {
        map_index: matchStats.currentMapIndex,
        team: parseInt(team.replace('team', '')),
        player_index: playerIndex,
        player_id: prev[team][playerIndex]?.playerId,
        player_name: playerName,
        stat_type: statType,
        value: newValue,
        all_stats: {
          ...prev[team][playerIndex],
          [statType]: newValue
        }
      }).catch(error => {
        console.error(' Failed to send SSE stats update:', error);
      });
      
      return updatedStats;
    });

    //  REMOVED AUTO-SAVE - User requested no auto-refresh
    // clearTimeout(window.statSaveTimeout);
    // window.statSaveTimeout = setTimeout(() => {
    //   saveMatchData();
    // }, 1000); // Save 1 second after last stat change
  };

