import React, { useState, useEffect, useCallback, Fragment } from 'react';
import { useAuth } from '../../hooks';
import { PlayerAvatar, getCountryFlag } from '../../utils/imageUtils';

function AdminPlayers({ navigateTo }) {
  const [players, setPlayers] = useState([]);
  const [teams, setTeams] = useState([]);
  const [loading, setLoading] = useState(true);
  const [filters, setFilters] = useState({
    search: '',
    role: 'all',
    team: 'all',
    region: 'all'
  });
  const [pagination, setPagination] = useState({
    current_page: 1,
    last_page: 1,
    per_page: 10,
    total: 0
  });
  const [expandedPlayer, setExpandedPlayer] = useState(null);
  const { api } = useAuth();

  const fetchData = useCallback(async (page = 1) => {
    try {
      setLoading(true);
      console.log(' AdminPlayers: Fetching players from real API...', { page, filters });
      
      // Build query parameters
      const params = new URLSearchParams({
        page: page.toString(),
        per_page: pagination.per_page.toString(),
        ...Object.fromEntries(Object.entries(filters).filter(([_, value]) => value && value !== 'all'))
      });
      
      // Fetch players and teams with pagination
      const [playersResponse, teamsResponse] = await Promise.all([
        api.get(`/admin/players?${params.toString()}`).catch(() => ({ data: { data: [], meta: {} } })),
        api.get('/teams').catch(() => ({ data: [] }))
      ]);

      // FIXED: Handle Laravel API response structure properly
      const playersData = playersResponse?.data?.data || playersResponse?.data || [];
      const teamsData = teamsResponse?.data?.data || teamsResponse?.data || teamsResponse || [];
      const meta = playersResponse?.data?.meta || { current_page: 1, last_page: 1, per_page: 10, total: playersData.length };

      console.log(' AdminPlayers: Real players data received:', playersData.length, 'players');
      console.log(' AdminPlayers: Pagination meta:', meta);
      console.log(' AdminPlayers: Sample player data:', playersData[0]);
      console.log(' AdminPlayers: Real teams data received:', teamsData.length, 'teams');

      setPlayers(playersData);
      setTeams(teamsData);
      setPagination(meta);
      console.log(' AdminPlayers: Players and teams loaded successfully');
    } catch (error) {
      console.error(' AdminPlayers: Error fetching data:', error);
      setPlayers([]);
      setTeams([]);
    } finally {
      setLoading(false);
    }
  }, [api, filters, pagination.per_page]);

  useEffect(() => {
    fetchData(1);
  }, [filters]); // Only re-fetch when filters change
  
  const handlePageChange = (page) => {
    fetchData(page);
  };
  
  const togglePlayerDetails = (playerId) => {
    setExpandedPlayer(expandedPlayer === playerId ? null : playerId);
  };

  const handleDelete = async (playerId, playerName) => {
    if (window.confirm(`Are you sure you want to delete "${playerName}"? This action cannot be undone.`)) {
      try {
        console.log(' AdminPlayers: Deleting player with ID:', playerId);
        // FIXED: Use POST with method spoofing for Laravel backend deletes
        await api.post(`/admin/players/${playerId}`, { _method: 'DELETE' });
        await fetchData(); // Refresh the list
        alert('Player deleted successfully!');
      } catch (error) {
        console.error('Error deleting player:', error);
        alert('Error deleting player. Please try again.');
      }
    }
  };

  const roles = ['all', 'Duelist', 'Vanguard', 'Strategist'];
  const regions = ['all', 'NA', 'EU', 'APAC'];
  const teamOptions = ['all', ...teams.map(team => team.short_name)];

  const getRoleColor = (role) => {
    switch (role) {
      case 'Duelist': return 'bg-red-100 text-red-800 dark:bg-red-900/20 dark:text-red-400';
      case 'Tank': return 'bg-blue-100 text-blue-800 dark:bg-blue-900/20 dark:text-blue-400';
      case 'Support': return 'bg-green-100 text-green-800 dark:bg-green-900/20 dark:text-green-400';
      case 'Controller': return 'bg-purple-100 text-purple-800 dark:bg-purple-900/20 dark:text-purple-400';
      default: return 'bg-gray-100 text-gray-800 dark:bg-gray-800 dark:text-gray-300';
    }
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center py-12">
        <div className="text-center">
          <div className="loading-spinner mx-auto mb-4"></div>
          <div className="text-gray-600 dark:text-gray-400">Loading players...</div>
        </div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h2 className="text-3xl font-bold text-gray-900 dark:text-white">Manage Players</h2>
          <p className="text-gray-600 dark:text-gray-400">Create, edit, and manage all players</p>
        </div>
        <button 
          onClick={() => navigateTo('admin-player-create')}
          className="btn btn-primary"
        >
          Add New Player
        </button>
      </div>

      {/* Filters */}
      <div className="card p-6">
        <div className="grid grid-cols-1 md:grid-cols-5 gap-4">
          <div>
            <label className="block text-sm font-medium mb-2 text-gray-900 dark:text-gray-100">
              Search Players
            </label>
            <input
              type="text"
              placeholder="Search by name..."
              value={filters.search}
              onChange={(e) => setFilters({...filters, search: e.target.value})}
              className="form-input"
            />
          </div>
          <div>
            <label className="block text-sm font-medium mb-2 text-gray-900 dark:text-gray-100">
              Role
            </label>
            <select
              value={filters.role}
              onChange={(e) => setFilters({...filters, role: e.target.value})}
              className="form-input"
            >
              {roles.map(role => (
                <option key={role} value={role}>
                  {role === 'all' ? 'All Roles' : role}
                </option>
              ))}
            </select>
          </div>
          <div>
            <label className="block text-sm font-medium mb-2 text-gray-900 dark:text-gray-100">
              Team
            </label>
            <select
              value={filters.team}
              onChange={(e) => setFilters({...filters, team: e.target.value})}
              className="form-input"
            >
              {teamOptions.map(team => (
                <option key={team} value={team}>
                  {team === 'all' ? 'All Teams' : team}
                </option>
              ))}
            </select>
          </div>
          <div>
            <label className="block text-sm font-medium mb-2 text-gray-900 dark:text-gray-100">
              Region
            </label>
            <select
              value={filters.region}
              onChange={(e) => setFilters({...filters, region: e.target.value})}
              className="form-input"
            >
              {regions.map(region => (
                <option key={region} value={region}>
                  {region === 'all' ? 'All Regions' : region}
                </option>
              ))}
            </select>
          </div>
          <div className="flex items-end">
            <button
              onClick={() => setFilters({ search: '', role: 'all', team: 'all', region: 'all' })}
              className="btn btn-secondary w-full"
            >
              Clear Filters
            </button>
          </div>
        </div>
      </div>

      {/* Players Table */}
      <div className="card overflow-hidden">
        <div className="overflow-x-auto">
          <table className="min-w-full divide-y divide-gray-200 dark:divide-gray-700">
            <thead className="bg-gray-50 dark:bg-gray-800">
              <tr>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-400 uppercase tracking-wider">
                  Player
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-400 uppercase tracking-wider">
                  Team
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-400 uppercase tracking-wider">
                  Role
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-400 uppercase tracking-wider">
                  Rating
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-400 uppercase tracking-wider">
                  Country
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-400 uppercase tracking-wider">
                  Actions
                </th>
              </tr>
            </thead>
            <tbody className="bg-white dark:bg-gray-900 divide-y divide-gray-200 dark:divide-gray-700">
              {players.map((player) => (
                <Fragment key={player.id}>
                  <tr className="hover:bg-gray-50 dark:hover:bg-gray-800">
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="flex items-center">
                      {/* FIXED: Use proper PlayerAvatar component instead of placeholder */}
                      <div className="mr-3">
                        <PlayerAvatar 
                          player={player} 
                          size="w-10 h-10" 
                          className="border border-gray-200 dark:border-gray-600"
                        />
                      </div>
                      <div>
                        <div className="text-sm font-medium text-gray-900 dark:text-white">
                          {player.username}
                        </div>
                        <div className="text-sm text-gray-500 dark:text-gray-400">
                          {player.real_name}
                        </div>
                        {/* FIXED: Show avatar path for debugging */}
                        {player.avatar_url && player.avatar_url.includes('/') && (
                          <div className="text-xs text-gray-400 dark:text-gray-500 truncate max-w-32">
                            {player.avatar_url}
                          </div>
                        )}
                      </div>
                    </div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="flex items-center space-x-2">
                      <span className="text-sm text-gray-900 dark:text-white">
                        {player.team?.short_name || 'Free Agent'}
                      </span>
                    </div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <span className={`px-2 py-1 text-xs font-medium rounded-full ${getRoleColor(player.role)}`}>
                      {player.role}
                    </span>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="text-sm font-medium text-gray-900 dark:text-white">
                      {player.rating?.toFixed(0) || 'N/A'}
                    </div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="flex items-center space-x-2">
                      <span className="text-lg">{player.flag || getCountryFlag(player.country)}</span>
                      <span className="text-sm text-gray-900 dark:text-white">{player.country}</span>
                    </div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
                    <div className="flex space-x-2">
                      <button
                        onClick={() => togglePlayerDetails(player.id)}
                        className="text-green-600 hover:text-green-900 dark:text-green-400 dark:hover:text-green-300"
                      >
                        {expandedPlayer === player.id ? 'Hide' : 'Details'}
                      </button>
                      <button
                        onClick={() => {
                          console.log(' AdminPlayers: Navigating to player-detail with ID:', player.id);
                          navigateTo('player-detail', { id: player.id });
                        }}
                        className="text-blue-600 hover:text-blue-900 dark:text-blue-400 dark:hover:text-blue-300"
                      >
                        View
                      </button>
                      <button
                        onClick={() => {
                          console.log(' AdminPlayers: Navigating to admin-player-edit with ID:', player.id);
                          navigateTo('admin-player-edit', { id: player.id });
                        }}
                        className="text-yellow-600 hover:text-yellow-900 dark:text-yellow-400 dark:hover:text-yellow-300"
                      >
                        Edit
                      </button>
                      <button
                        onClick={() => handleDelete(player.id, player.username)}
                        className="text-red-600 hover:text-red-900 dark:text-red-400 dark:hover:text-red-300"
                      >
                        Delete
                      </button>
                    </div>
                  </td>
                  </tr>
                  {/* Player Profile Details Row */}
                  {expandedPlayer === player.id && (
                  <tr className="bg-gray-50 dark:bg-gray-800">
                    <td colSpan="6" className="px-6 py-4">
                      <div className="bg-white dark:bg-gray-900 rounded-lg p-4 border border-gray-200 dark:border-gray-700">
                        <h4 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">
                          Complete Player Profile - {player.username}
                        </h4>
                        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                          <div>
                            <div className="text-sm font-medium text-gray-500 dark:text-gray-400">Real Name</div>
                            <div className="text-sm text-gray-900 dark:text-white">{player.real_name || 'N/A'}</div>
                          </div>
                          <div>
                            <div className="text-sm font-medium text-gray-500 dark:text-gray-400">Age</div>
                            <div className="text-sm text-gray-900 dark:text-white">{player.age || 'N/A'}</div>
                          </div>
                          <div>
                            <div className="text-sm font-medium text-gray-500 dark:text-gray-400">Total Earnings</div>
                            <div className="text-sm text-green-600 dark:text-green-400 font-semibold">
                              ${(player.earnings || player.total_earnings || 0).toLocaleString()}
                            </div>
                          </div>
                          <div>
                            <div className="text-sm font-medium text-gray-500 dark:text-gray-400">Main Hero</div>
                            <div className="text-sm text-gray-900 dark:text-white">{player.main_hero || 'N/A'}</div>
                          </div>
                          <div>
                            <div className="text-sm font-medium text-gray-500 dark:text-gray-400">Status</div>
                            <div className="text-sm text-gray-900 dark:text-white">{player.status || 'active'}</div>
                          </div>
                          <div>
                            <div className="text-sm font-medium text-gray-500 dark:text-gray-400">Joined Team</div>
                            <div className="text-sm text-gray-900 dark:text-white">
                              {player.joined_team_at ? new Date(player.joined_team_at).toLocaleDateString() : 'N/A'}
                            </div>
                          </div>
                        </div>
                        {player.biography && (
                          <div className="mt-4">
                            <div className="text-sm font-medium text-gray-500 dark:text-gray-400 mb-2">Biography</div>
                            <div className="text-sm text-gray-900 dark:text-white bg-gray-50 dark:bg-gray-800 p-3 rounded">
                              {player.biography}
                            </div>
                          </div>
                        )}
                        {player.social_media && Object.keys(player.social_media).length > 0 && (
                          <div className="mt-4">
                            <div className="text-sm font-medium text-gray-500 dark:text-gray-400 mb-2">Social Media</div>
                            <div className="flex flex-wrap gap-2">
                              {Object.entries(player.social_media).map(([platform, url]) => (
                                url && (
                                  <a
                                    key={platform}
                                    href={url}
                                    target="_blank"
                                    rel="noopener noreferrer"
                                    className="px-2 py-1 text-xs bg-blue-100 dark:bg-blue-900/30 text-blue-800 dark:text-blue-400 rounded hover:bg-blue-200 dark:hover:bg-blue-900/50"
                                  >
                                    {platform.charAt(0).toUpperCase() + platform.slice(1)}
                                  </a>
                                )
                              ))}
                            </div>
                          </div>
                        )}
                      </div>
                    </td>
                  </tr>
                  )}
                </Fragment>
              ))}
            </tbody>
          </table>
        </div>
      </div>

      {/* No Results */}
      {players.length === 0 && (
        <div className="card p-12 text-center">
          <div className="text-6xl mb-4"></div>
          <h3 className="text-xl font-semibold text-gray-900 dark:text-white mb-2">No Players Found</h3>
          <p className="text-gray-600 dark:text-gray-400 mb-4">
            {filters.search || filters.role !== 'all' || filters.team !== 'all' || filters.region !== 'all'
              ? 'Try adjusting your filters to find more players.'
              : 'Get started by adding your first player.'}
          </p>
          <button
            onClick={() => navigateTo('admin-player-create')}
            className="btn btn-primary"
          >
            Add First Player
          </button>
        </div>
      )}

      {/* Pagination */}
      {pagination.last_page > 1 && (
        <div className="card p-4">
          <div className="flex items-center justify-between">
            <div className="text-sm text-gray-600 dark:text-gray-400">
              Showing {((pagination.current_page - 1) * pagination.per_page) + 1} to {Math.min(pagination.current_page * pagination.per_page, pagination.total)} of {pagination.total} players
            </div>
            <div className="flex items-center space-x-2">
              <button
                onClick={() => handlePageChange(pagination.current_page - 1)}
                disabled={pagination.current_page <= 1}
                className="px-3 py-1 text-sm bg-gray-200 dark:bg-gray-700 text-gray-700 dark:text-gray-300 rounded hover:bg-gray-300 dark:hover:bg-gray-600 disabled:opacity-50 disabled:cursor-not-allowed"
              >
                Previous
              </button>
              {Array.from({ length: Math.min(5, pagination.last_page) }, (_, i) => {
                const page = i + Math.max(1, pagination.current_page - 2);
                if (page > pagination.last_page) return null;
                return (
                  <button
                    key={page}
                    onClick={() => handlePageChange(page)}
                    className={`px-3 py-1 text-sm rounded ${
                      pagination.current_page === page
                        ? 'bg-red-600 text-white'
                        : 'bg-gray-200 dark:bg-gray-700 text-gray-700 dark:text-gray-300 hover:bg-gray-300 dark:hover:bg-gray-600'
                    }`}
                  >
                    {page}
                  </button>
                );
              })}
              <button
                onClick={() => handlePageChange(pagination.current_page + 1)}
                disabled={pagination.current_page >= pagination.last_page}
                className="px-3 py-1 text-sm bg-gray-200 dark:bg-gray-700 text-gray-700 dark:text-gray-300 rounded hover:bg-gray-300 dark:hover:bg-gray-600 disabled:opacity-50 disabled:cursor-not-allowed"
              >
                Next
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Player Stats */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
        <div className="card p-6 text-center">
          <div className="text-3xl mb-2"></div>
          <div className="text-2xl font-bold text-blue-600 dark:text-blue-400">
            {pagination.total || players.length}
          </div>
          <div className="text-sm text-gray-600 dark:text-gray-400">Total Players</div>
        </div>
        <div className="card p-6 text-center">
          <div className="text-3xl mb-2"></div>
          <div className="text-2xl font-bold text-red-600 dark:text-red-400">
            {players.filter(p => p.role === 'Duelist').length}
          </div>
          <div className="text-sm text-gray-600 dark:text-gray-400">Duelists</div>
        </div>
        <div className="card p-6 text-center">
          <div className="text-3xl mb-2"></div>
          <div className="text-2xl font-bold text-blue-600 dark:text-blue-400">
            {players.filter(p => p.role === 'Vanguard').length}
          </div>
          <div className="text-sm text-gray-600 dark:text-gray-400">Vanguards</div>
        </div>
        <div className="card p-6 text-center">
          <div className="text-3xl mb-2"></div>
          <div className="text-2xl font-bold text-green-600 dark:text-green-400">
            {players.filter(p => p.role === 'Strategist').length}
          </div>
          <div className="text-sm text-gray-600 dark:text-gray-400">Strategists</div>
        </div>
      </div>
    </div>
  );
}

export default AdminPlayers;