import React, { useState, useEffect, useRef } from 'react';
import { 
  Home, Calendar, Trophy, Users, Menu, X, Search, 
  ChevronRight, Bell, Settings, LogOut, User, Shield,
  TrendingUp, MessageSquare, Newspaper, BarChart3
} from 'lucide-react';
import { useAuth } from '../../hooks';
import UserAvatar from '../common/UserAvatar';

// VLR.gg-style Mobile Navigation
export const MobileNavigation = ({ currentPage, navigateTo, user }) => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [searchQuery, setSearchQuery] = useState('');
  const [showSearch, setShowSearch] = useState(false);
  const searchInputRef = useRef(null);
  const { logout, isAdmin } = useAuth();

  // Close menu when page changes
  useEffect(() => {
    setIsMenuOpen(false);
  }, [currentPage]);

  // Focus search when opened
  useEffect(() => {
    if (showSearch && searchInputRef.current) {
      searchInputRef.current.focus();
    }
  }, [showSearch]);

  // Prevent body scroll when menu is open
  useEffect(() => {
    if (isMenuOpen) {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.overflow = '';
    }
    return () => {
      document.body.style.overflow = '';
    };
  }, [isMenuOpen]);

  const handleSearch = () => {
    if (searchQuery.trim()) {
      navigateTo('search', { query: searchQuery.trim() });
      setSearchQuery('');
      setShowSearch(false);
    }
  };

  const handleLogout = () => {
    logout();
    setIsMenuOpen(false);
    navigateTo('home');
  };

  // Main navigation items
  const mainNavItems = [
    { id: 'home', icon: Home, label: 'Home' },
    { id: 'matches', icon: Calendar, label: 'Matches' },
    { id: 'events', icon: Trophy, label: 'Events' },
    { id: 'teams', icon: Users, label: 'Teams' },
    { id: 'rankings', icon: BarChart3, label: 'Rankings' }
  ];

  // Secondary navigation items
  const secondaryNavItems = [
    { id: 'news', icon: Newspaper, label: 'News' },
    { id: 'forums', icon: MessageSquare, label: 'Forums' },
    { id: 'stats', icon: TrendingUp, label: 'Stats' }
  ];

  return (
    <>
      {/* Mobile Header - VLR.gg style */}
      <header className="lg:hidden fixed top-0 left-0 right-0 bg-white dark:bg-gray-900 border-b border-gray-200 dark:border-gray-700 z-50">
        <div className="flex items-center justify-between h-14 px-4">
          {/* Logo */}
          <button 
            onClick={() => navigateTo('home')}
            className="flex items-center space-x-2"
          >
            <div className="w-8 h-8 bg-gradient-to-br from-red-600 to-red-700 rounded flex items-center justify-center">
              <span className="text-white font-bold text-sm">M</span>
            </div>
            <span className="font-bold text-lg">MRVL</span>
          </button>

          {/* Right Actions */}
          <div className="flex items-center space-x-2">
            {/* Search Toggle */}
            <button
              onClick={() => setShowSearch(!showSearch)}
              className="touch-target touch-optimized p-2 rounded-lg hover:bg-gray-100 dark:hover:bg-gray-800 transition-colors"
              aria-label="Search"
            >
              <Search className="w-5 h-5" />
            </button>

            {/* Notifications */}
            {user && (
              <button
                onClick={() => navigateTo('notifications')}
                className="touch-target touch-optimized p-2 rounded-lg hover:bg-gray-100 dark:hover:bg-gray-800 transition-colors relative"
                aria-label="Notifications"
              >
                <Bell className="w-5 h-5" />
                <span className="absolute top-1 right-1 w-2 h-2 bg-red-500 rounded-full"></span>
              </button>
            )}

            {/* Menu Toggle */}
            <button
              onClick={() => setIsMenuOpen(!isMenuOpen)}
              className="touch-target touch-optimized p-2 rounded-lg hover:bg-gray-100 dark:hover:bg-gray-800 transition-colors"
              aria-label="Menu"
            >
              <Menu className="w-5 h-5" />
            </button>
          </div>
        </div>

        {/* Search Bar - Expandable */}
        {showSearch && (
          <div className="px-4 py-2 border-t border-gray-200 dark:border-gray-700">
            <form onSubmit={(e) => { e.preventDefault(); handleSearch(); }} className="relative">
              <input
                ref={searchInputRef}
                type="search"
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                placeholder="Search teams, players, events..."
                className="w-full px-10 py-2 bg-gray-100 dark:bg-gray-800 rounded-lg text-sm focus:outline-none focus:ring-2 focus:ring-red-500"
              />
              <Search className="absolute left-3 top-2.5 w-4 h-4 text-gray-400" />
              <button
                type="button"
                onClick={() => { setShowSearch(false); setSearchQuery(''); }}
                className="absolute right-3 top-2.5 text-gray-400 hover:text-gray-600"
              >
                <X className="w-4 h-4" />
              </button>
            </form>
          </div>
        )}
      </header>

      {/* Bottom Navigation - VLR.gg style */}
      <nav className="lg:hidden fixed bottom-0 left-0 right-0 bg-white dark:bg-gray-900 border-t border-gray-200 dark:border-gray-700 z-50">
        <div className="grid grid-cols-5 h-16">
          {mainNavItems.map(item => {
            const isActive = currentPage === item.id;
            return (
              <button
                key={item.id}
                onClick={() => navigateTo(item.id)}
                className={`touch-target touch-optimized flex flex-col items-center justify-center space-y-1 transition-colors ${
                  isActive 
                    ? 'text-red-600 dark:text-red-400' 
                    : 'text-gray-600 dark:text-gray-400'
                }`}
              >
                <item.icon className={`w-5 h-5 ${isActive ? 'transform scale-110' : ''}`} />
                <span className="text-xs font-medium">{item.label}</span>
              </button>
            );
          })}
        </div>
      </nav>

      {/* Full Screen Menu Overlay */}
      {isMenuOpen && (
        <>
          {/* Backdrop */}
          <div 
            className="lg:hidden fixed inset-0 bg-black bg-opacity-50 z-40"
            onClick={() => setIsMenuOpen(false)}
          />

          {/* Menu Panel */}
          <div className="lg:hidden fixed inset-y-0 right-0 w-full max-w-sm bg-white dark:bg-gray-900 shadow-xl z-50 overflow-y-auto">
            {/* Menu Header */}
            <div className="sticky top-0 bg-white dark:bg-gray-900 border-b border-gray-200 dark:border-gray-700 p-4">
              <div className="flex items-center justify-between">
                <h2 className="text-lg font-bold">Menu</h2>
                <button
                  onClick={() => setIsMenuOpen(false)}
                  className="p-2 rounded-lg hover:bg-gray-100 dark:hover:bg-gray-800 transition-colors"
                >
                  <X className="w-5 h-5" />
                </button>
              </div>
            </div>

            {/* User Section */}
            {user ? (
              <div className="p-4 border-b border-gray-200 dark:border-gray-700">
                <button
                  onClick={() => {
                    navigateTo('user-profile', { id: user.id });
                    setIsMenuOpen(false);
                  }}
                  className="flex items-center space-x-3 w-full"
                >
                  <UserAvatar user={user} size="md" />
                  <div className="flex-1 text-left">
                    <div className="font-semibold">{user.name}</div>
                    <div className="text-sm text-gray-500 dark:text-gray-400">View Profile</div>
                  </div>
                  <ChevronRight className="w-5 h-5 text-gray-400" />
                </button>
              </div>
            ) : (
              <div className="p-4 border-b border-gray-200 dark:border-gray-700">
                <button
                  onClick={() => {
                    navigateTo('login');
                    setIsMenuOpen(false);
                  }}
                  className="w-full bg-red-600 hover:bg-red-700 text-white font-medium py-3 rounded-lg transition-colors"
                >
                  Sign In
                </button>
              </div>
            )}

            {/* Navigation Sections */}
            <div className="p-4">
              {/* Main Navigation */}
              <div className="mb-6">
                <h3 className="text-xs font-semibold text-gray-500 dark:text-gray-400 uppercase tracking-wider mb-2">
                  Main
                </h3>
                <div className="space-y-1">
                  {mainNavItems.map(item => (
                    <MenuButton
                      key={item.id}
                      icon={item.icon}
                      label={item.label}
                      active={currentPage === item.id}
                      onClick={() => {
                        navigateTo(item.id);
                        setIsMenuOpen(false);
                      }}
                    />
                  ))}
                </div>
              </div>

              {/* Secondary Navigation */}
              <div className="mb-6">
                <h3 className="text-xs font-semibold text-gray-500 dark:text-gray-400 uppercase tracking-wider mb-2">
                  Community
                </h3>
                <div className="space-y-1">
                  {secondaryNavItems.map(item => (
                    <MenuButton
                      key={item.id}
                      icon={item.icon}
                      label={item.label}
                      active={currentPage === item.id}
                      onClick={() => {
                        navigateTo(item.id);
                        setIsMenuOpen(false);
                      }}
                    />
                  ))}
                </div>
              </div>

              {/* User Actions */}
              {user && (
                <div className="mb-6">
                  <h3 className="text-xs font-semibold text-gray-500 dark:text-gray-400 uppercase tracking-wider mb-2">
                    Account
                  </h3>
                  <div className="space-y-1">
                    {isAdmin() && (
                      <MenuButton
                        icon={Shield}
                        label="Admin Dashboard"
                        onClick={() => {
                          navigateTo('admin-dashboard');
                          setIsMenuOpen(false);
                        }}
                      />
                    )}
                    <MenuButton
                      icon={Settings}
                      label="Settings"
                      onClick={() => {
                        navigateTo('settings');
                        setIsMenuOpen(false);
                      }}
                    />
                    <MenuButton
                      icon={LogOut}
                      label="Sign Out"
                      onClick={handleLogout}
                      className="text-red-600 dark:text-red-400"
                    />
                  </div>
                </div>
              )}
            </div>

            {/* Footer */}
            <div className="p-4 border-t border-gray-200 dark:border-gray-700 text-center text-sm text-gray-500 dark:text-gray-400">
              <p> 2025 MRVL. All rights reserved.</p>
            </div>
          </div>
        </>
      )}
    </>
  );
};

// Menu Button Component
const MenuButton = ({ icon: Icon, label, active, onClick, className = '' }) => {
  return (
    <button
      onClick={onClick}
      className={`flex items-center space-x-3 w-full px-3 py-2.5 rounded-lg transition-colors ${
        active 
          ? 'bg-red-50 dark:bg-red-900/20 text-red-600 dark:text-red-400' 
          : 'hover:bg-gray-100 dark:hover:bg-gray-800'
      } ${className}`}
    >
      <Icon className="w-5 h-5" />
      <span className="font-medium">{label}</span>
    </button>
  );
};

export default MobileNavigation;