import React, { useState, useEffect, useCallback, Fragment } from 'react';
import { useAuth } from '../../hooks';
import { TeamLogo } from '../../utils/imageUtils';

function AdminTeams({ navigateTo }) {
  const [teams, setTeams] = useState([]);
  const [loading, setLoading] = useState(true);
  const [filters, setFilters] = useState({
    search: '',
    region: 'all',
    sortBy: 'rating'
  });
  const [pagination, setPagination] = useState({
    current_page: 1,
    last_page: 1,
    per_page: 10,
    total: 0
  });
  const [expandedTeam, setExpandedTeam] = useState(null);
  const { api } = useAuth();

  const fetchTeams = useCallback(async (page = 1) => {
    try {
      setLoading(true);
      console.log(' AdminTeams: Fetching teams from real API...', { page, filters });
      
      const params = new URLSearchParams({
        page: page.toString(),
        per_page: pagination.per_page.toString(),
        sort_by: filters.sortBy
      });
      if (filters.search) params.append('search', filters.search);
      if (filters.region !== 'all') params.append('region', filters.region);
      
      const response = await api.get(`/admin/teams?${params.toString()}`);
      
      // FIXED: Handle Laravel API response structure properly
      const teamsData = response?.data?.data || response?.data || [];
      const meta = response?.data?.meta || { current_page: 1, last_page: 1, per_page: 10, total: teamsData.length };
      
      console.log(' AdminTeams: Real teams data received:', teamsData.length, 'teams');
      console.log(' AdminTeams: Pagination meta:', meta);
      console.log(' AdminTeams: Sample team data:', teamsData[0]);
      
      // Ensure we have an array
      if (!Array.isArray(teamsData)) {
        console.warn(' AdminTeams: Teams data is not an array, using fallback');
        setTeams([]);
        return;
      }
      
      setTeams(teamsData);
      setPagination(meta);
      console.log(' AdminTeams: Teams loaded successfully');
    } catch (error) {
      console.error('Error fetching teams:', error);
      setTeams([]);
    } finally {
      setLoading(false);
    }
  }, [api, filters, pagination.per_page]);

  useEffect(() => {
    fetchTeams(1);
  }, [filters]); // Only re-fetch when filters change
  
  const handlePageChange = (page) => {
    fetchTeams(page);
  };
  
  const toggleTeamDetails = (teamId) => {
    setExpandedTeam(expandedTeam === teamId ? null : teamId);
  };

  const handleDelete = async (teamId, teamName) => {
    if (window.confirm(`Are you sure you want to delete "${teamName}"? This action cannot be undone.`)) {
      try {
        // Check if the team exists in our current teams list first
        const teamExists = teams.find(team => team.id === teamId);
        if (!teamExists) {
          alert('Team not found. Please refresh the page and try again.');
          await fetchTeams(); // Refresh the list
          return;
        }

        console.log(' AdminTeams: Deleting team with ID:', teamId);
        // FIXED: Use POST with method spoofing for Laravel backend deletes
        await api.post(`/admin/teams/${teamId}`, { _method: 'DELETE' });
        await fetchTeams(); // Refresh the list
        alert('Team deleted successfully!');
      } catch (error) {
        console.error('Error deleting team:', error);
        
        if (error.response && error.response.status === 404) {
          alert('Team not found. It may have already been deleted. Refreshing the list...');
          await fetchTeams(); // Refresh the list
        } else if (error.response && error.response.status === 400) {
          const errorData = error.response.data;
          
          if (errorData.can_force) {
            const playerCount = errorData.player_count || 0;
            const matchCount = errorData.match_count || 0;
            
            let message = errorData.message + '\n\n';
            if (playerCount > 0) {
              message += `This team has ${playerCount} active player(s) who will be moved to Free Agent status.\n`;
            }
            if (matchCount > 0) {
              message += `This team has ${matchCount} match(es) in history.\n`;
            }
            message += '\nDo you want to force delete this team?';
            
            // eslint-disable-next-line no-restricted-globals
            if (window.confirm(message)) {
              try {
                await api.post(`/admin/teams/${teamId}`, { _method: 'DELETE', force: true });
                await fetchTeams();
                alert('Team force deleted successfully!');
              } catch (forceError) {
                console.error('Error force deleting team:', forceError);
                alert('Error force deleting team. Please try again.');
              }
            }
          } else {
            alert(errorData.message || 'Cannot delete team. Please check for dependencies.');
          }
        } else {
          alert('Error deleting team. Please try again.');
        }
      }
    }
  };

  const regions = ['all', 'NA', 'EU', 'APAC'];

  if (loading) {
    return (
      <div className="flex items-center justify-center py-12">
        <div className="text-center">
          <div className="loading-spinner mx-auto mb-4"></div>
          <div className="text-gray-600 dark:text-gray-400">Loading teams...</div>
        </div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h2 className="text-3xl font-bold text-gray-900 dark:text-white">Manage Teams</h2>
          <p className="text-gray-600 dark:text-gray-400">Create, edit, and manage all teams</p>
        </div>
        <button 
          onClick={() => navigateTo('admin-team-create')}
          className="btn btn-primary"
        >
          Create New Team
        </button>
      </div>

      {/* Filters */}
      <div className="card p-6">
        <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
          <div>
            <label className="block text-sm font-medium mb-2 text-gray-900 dark:text-gray-100">
              Search Teams
            </label>
            <input
              type="text"
              placeholder="Search by name..."
              value={filters.search}
              onChange={(e) => setFilters({...filters, search: e.target.value})}
              className="form-input"
            />
          </div>
          <div>
            <label className="block text-sm font-medium mb-2 text-gray-900 dark:text-gray-100">
              Region
            </label>
            <select
              value={filters.region}
              onChange={(e) => setFilters({...filters, region: e.target.value})}
              className="form-input"
            >
              {regions.map(region => (
                <option key={region} value={region}>
                  {region === 'all' ? 'All Regions' : region}
                </option>
              ))}
            </select>
          </div>
          <div>
            <label className="block text-sm font-medium mb-2 text-gray-900 dark:text-gray-100">
              Sort By
            </label>
            <select
              value={filters.sortBy}
              onChange={(e) => setFilters({...filters, sortBy: e.target.value})}
              className="form-input"
            >
              <option value="rating">Rating</option>
              <option value="name">Name</option>
            </select>
          </div>
          <div className="flex items-end">
            <button
              onClick={() => setFilters({ search: '', region: 'all', sortBy: 'rating' })}
              className="btn btn-secondary w-full"
            >
              Clear Filters
            </button>
          </div>
        </div>
      </div>

      {/* Teams Table */}
      <div className="card overflow-hidden">
        <div className="overflow-x-auto">
          <table className="min-w-full divide-y divide-gray-200 dark:divide-gray-700">
            <thead className="bg-gray-50 dark:bg-gray-800">
              <tr>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-400 uppercase tracking-wider">
                  Team
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-400 uppercase tracking-wider">
                  Region
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-400 uppercase tracking-wider">
                  Rating
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-400 uppercase tracking-wider">
                  Players
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-400 uppercase tracking-wider">
                  Actions
                </th>
              </tr>
            </thead>
            <tbody className="bg-white dark:bg-gray-900 divide-y divide-gray-200 dark:divide-gray-700">
              {teams.map((team) => (
                <Fragment key={team.id}>
                  <tr className="hover:bg-gray-50 dark:hover:bg-gray-800">
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="flex items-center">
                      {/* FIXED: Use proper TeamLogo component instead of raw emoji/text */}
                      <div className="mr-3">
                        <TeamLogo 
                          team={team} 
                          size="w-10 h-10" 
                          className="border border-gray-200 dark:border-gray-600"
                        />
                      </div>
                      <div>
                        <div className="text-sm font-medium text-gray-900 dark:text-white">
                          {team.short_name}
                        </div>
                        <div className="text-sm text-gray-500 dark:text-gray-400">
                          {team.name}
                        </div>
                      </div>
                    </div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <span className="px-2 py-1 text-xs font-medium bg-blue-100 text-blue-800 dark:bg-blue-900/20 dark:text-blue-400 rounded-full">
                      {team.region}
                    </span>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="text-sm text-gray-900 dark:text-white font-medium">
                      {team.rating || 'N/A'}
                    </div>
                    <div className="text-sm text-gray-500 dark:text-gray-400">
                      Rank #{team.rank || 'N/A'}
                    </div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="text-sm text-gray-900 dark:text-white">
                      {team.player_count || 0} players
                    </div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
                    <div className="flex space-x-2">
                      <button
                        onClick={() => toggleTeamDetails(team.id)}
                        className="text-green-600 hover:text-green-900 dark:text-green-400 dark:hover:text-green-300"
                      >
                        {expandedTeam === team.id ? 'Hide' : 'Details'}
                      </button>
                      <button
                        onClick={() => {
                          console.log(' AdminTeams: Navigating to team-detail with ID:', team.id);
                          navigateTo('team-detail', { id: team.id });
                        }}
                        className="text-blue-600 hover:text-blue-900 dark:text-blue-400 dark:hover:text-blue-300"
                      >
                        View
                      </button>
                      <button
                        onClick={() => {
                          console.log(' AdminTeams: Navigating to admin-team-edit with ID:', team.id);
                          navigateTo('admin-team-edit', { id: team.id });
                        }}
                        className="text-yellow-600 hover:text-yellow-900 dark:text-yellow-400 dark:hover:text-yellow-300"
                      >
                        Edit
                      </button>
                      <button
                        onClick={() => handleDelete(team.id, team.name)}
                        className="text-red-600 hover:text-red-900 dark:text-red-400 dark:hover:text-red-300"
                      >
                        Delete
                      </button>
                    </div>
                  </td>
                  </tr>
                  {/* Team Profile Details Row */}
                  {expandedTeam === team.id && (
                  <tr className="bg-gray-50 dark:bg-gray-800">
                    <td colSpan="5" className="px-6 py-4">
                      <div className="bg-white dark:bg-gray-900 rounded-lg p-4 border border-gray-200 dark:border-gray-700">
                        <h4 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">
                          Complete Team Profile - {team.name}
                        </h4>
                        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                          <div>
                            <div className="text-sm font-medium text-gray-500 dark:text-gray-400">Founded</div>
                            <div className="text-sm text-gray-900 dark:text-white">{team.founded || 'N/A'}</div>
                          </div>
                          <div>
                            <div className="text-sm font-medium text-gray-500 dark:text-gray-400">Captain</div>
                            <div className="text-sm text-gray-900 dark:text-white">{team.captain || 'N/A'}</div>
                          </div>
                          <div>
                            <div className="text-sm font-medium text-gray-500 dark:text-gray-400">Coach</div>
                            <div className="text-sm text-gray-900 dark:text-white">{team.coach || 'N/A'}</div>
                          </div>
                          <div>
                            <div className="text-sm font-medium text-gray-500 dark:text-gray-400">Total Earnings</div>
                            <div className="text-sm text-green-600 dark:text-green-400 font-semibold">
                              ${(team.earnings || team.total_earnings || 0).toLocaleString()}
                            </div>
                          </div>
                          <div>
                            <div className="text-sm font-medium text-gray-500 dark:text-gray-400">Record (W-L)</div>
                            <div className="text-sm text-gray-900 dark:text-white">
                              {team.wins || 0}-{team.losses || 0}
                            </div>
                          </div>
                          <div>
                            <div className="text-sm font-medium text-gray-500 dark:text-gray-400">Status</div>
                            <div className="text-sm text-gray-900 dark:text-white">{team.status || 'active'}</div>
                          </div>
                        </div>
                        {team.website && (
                          <div className="mt-4">
                            <div className="text-sm font-medium text-gray-500 dark:text-gray-400 mb-2">Website</div>
                            <a
                              href={team.website.startsWith('http') ? team.website : `https://${team.website}`}
                              target="_blank"
                              rel="noopener noreferrer"
                              className="text-sm text-blue-600 dark:text-blue-400 hover:underline"
                            >
                              {team.website}
                            </a>
                          </div>
                        )}
                        {team.social_media && Object.keys(team.social_media).length > 0 && (
                          <div className="mt-4">
                            <div className="text-sm font-medium text-gray-500 dark:text-gray-400 mb-2">Social Media</div>
                            <div className="flex flex-wrap gap-2">
                              {Object.entries(team.social_media).map(([platform, url]) => (
                                url && (
                                  <a
                                    key={platform}
                                    href={url}
                                    target="_blank"
                                    rel="noopener noreferrer"
                                    className="px-2 py-1 text-xs bg-blue-100 dark:bg-blue-900/30 text-blue-800 dark:text-blue-400 rounded hover:bg-blue-200 dark:hover:bg-blue-900/50"
                                  >
                                    {platform.charAt(0).toUpperCase() + platform.slice(1)}
                                  </a>
                                )
                              ))}
                            </div>
                          </div>
                        )}
                      </div>
                    </td>
                  </tr>
                  )}
                </Fragment>
              ))}
            </tbody>
          </table>
        </div>
      </div>

      {/* No Results */}
      {teams.length === 0 && (
        <div className="card p-12 text-center">
          <div className="text-6xl mb-4"></div>
          <h3 className="text-xl font-semibold text-gray-900 dark:text-white mb-2">No Teams Found</h3>
          <p className="text-gray-600 dark:text-gray-400 mb-4">
            {filters.search || filters.region !== 'all' 
              ? 'Try adjusting your filters to find more teams.'
              : 'Get started by creating your first team.'}
          </p>
          <button
            onClick={() => navigateTo('admin-team-create')}
            className="btn btn-primary"
          >
            Create First Team
          </button>
        </div>
      )}

      {/* Pagination */}
      {pagination.last_page > 1 && (
        <div className="card p-4">
          <div className="flex items-center justify-between">
            <div className="text-sm text-gray-600 dark:text-gray-400">
              Showing {((pagination.current_page - 1) * pagination.per_page) + 1} to {Math.min(pagination.current_page * pagination.per_page, pagination.total)} of {pagination.total} teams
            </div>
            <div className="flex items-center space-x-2">
              <button
                onClick={() => handlePageChange(pagination.current_page - 1)}
                disabled={pagination.current_page <= 1}
                className="px-3 py-1 text-sm bg-gray-200 dark:bg-gray-700 text-gray-700 dark:text-gray-300 rounded hover:bg-gray-300 dark:hover:bg-gray-600 disabled:opacity-50 disabled:cursor-not-allowed"
              >
                Previous
              </button>
              {Array.from({ length: Math.min(5, pagination.last_page) }, (_, i) => {
                const page = i + Math.max(1, pagination.current_page - 2);
                if (page > pagination.last_page) return null;
                return (
                  <button
                    key={page}
                    onClick={() => handlePageChange(page)}
                    className={`px-3 py-1 text-sm rounded ${
                      pagination.current_page === page
                        ? 'bg-red-600 text-white'
                        : 'bg-gray-200 dark:bg-gray-700 text-gray-700 dark:text-gray-300 hover:bg-gray-300 dark:hover:bg-gray-600'
                    }`}
                  >
                    {page}
                  </button>
                );
              })}
              <button
                onClick={() => handlePageChange(pagination.current_page + 1)}
                disabled={pagination.current_page >= pagination.last_page}
                className="px-3 py-1 text-sm bg-gray-200 dark:bg-gray-700 text-gray-700 dark:text-gray-300 rounded hover:bg-gray-300 dark:hover:bg-gray-600 disabled:opacity-50 disabled:cursor-not-allowed"
              >
                Next
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Team Statistics */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
        <div className="card p-6 text-center">
          <div className="text-3xl mb-2"></div>
          <div className="text-2xl font-bold text-blue-600 dark:text-blue-400">
            {pagination.total || teams.length}
          </div>
          <div className="text-sm text-gray-600 dark:text-gray-400">Total Teams</div>
        </div>
        <div className="card p-6 text-center">
          <div className="text-3xl mb-2"></div>
          <div className="text-2xl font-bold text-green-600 dark:text-green-400">
            {teams.filter(t => t.region === 'NA').length}
          </div>
          <div className="text-sm text-gray-600 dark:text-gray-400">NA Teams</div>
        </div>
        <div className="card p-6 text-center">
          <div className="text-3xl mb-2"></div>
          <div className="text-2xl font-bold text-purple-600 dark:text-purple-400">
            {teams.filter(t => t.region === 'EU').length}
          </div>
          <div className="text-sm text-gray-600 dark:text-gray-400">EU Teams</div>
        </div>
        <div className="card p-6 text-center">
          <div className="text-3xl mb-2"></div>
          <div className="text-2xl font-bold text-yellow-600 dark:text-yellow-400">
            {teams.filter(t => (t.rating || 0) > 2000).length}
          </div>
          <div className="text-sm text-gray-600 dark:text-gray-400">High Rated</div>
        </div>
      </div>
    </div>
  );
}

export default AdminTeams;