import React, { useState, useEffect } from 'react';
import { TeamLogo, getCountryFlag } from '../utils/imageUtils';
import { getImageUrl } from '../utils/imageUrlUtils';

function MatchCard({ match, navigateTo, isCompact = false }) {
  // State for live updates
  const [liveMatch, setLiveMatch] = useState(match);

  // Update state when match prop changes
  useEffect(() => {
    setLiveMatch(match);
  }, [match]);

  // Listen for live updates
  useEffect(() => {
    const handleMatchUpdate = (event) => {
      if (event.detail && event.detail.matchId === match.id) {
        console.log(` MatchCard ${match.id} - Live update received:`, event.detail);
        
        // Update match data based on the update type
        setLiveMatch(prev => {
          const updated = { ...prev };
          
          switch (event.detail.updateType) {
            case 'SCORE_UPDATE':
              if (event.detail.seriesScore) {
                updated.series_score_team1 = event.detail.seriesScore.team1;
                updated.series_score_team2 = event.detail.seriesScore.team2;
                updated.team1_score = event.detail.seriesScore.team1;
                updated.team2_score = event.detail.seriesScore.team2;
              }
              break;
            
            case 'STATUS_UPDATE':
              if (event.detail.status) {
                updated.status = event.detail.status;
              }
              if (event.detail.winnerId) {
                updated.winner_id = event.detail.winnerId;
              }
              break;
            
            case 'MAP_COMPLETED':
              if (event.detail.seriesScore) {
                updated.series_score_team1 = event.detail.seriesScore.team1;
                updated.series_score_team2 = event.detail.seriesScore.team2;
                updated.team1_score = event.detail.seriesScore.team1;
                updated.team2_score = event.detail.seriesScore.team2;
              }
              if (event.detail.mapsData) {
                updated.maps_data = event.detail.mapsData;
              }
              break;
            
            case 'MATCH_COMPLETED':
              updated.status = 'completed';
              if (event.detail.winnerId) {
                updated.winner_id = event.detail.winnerId;
              }
              if (event.detail.finalScore) {
                updated.series_score_team1 = event.detail.finalScore.team1;
                updated.series_score_team2 = event.detail.finalScore.team2;
                updated.team1_score = event.detail.finalScore.team1;
                updated.team2_score = event.detail.finalScore.team2;
              }
              break;
          }
          
          return updated;
        });
      }
    };

    // Listen for both custom events and storage events
    window.addEventListener('mrvl-match-updated', handleMatchUpdate);
    
    // Also listen for storage events for cross-tab updates
    const handleStorageUpdate = (e) => {
      if (e.key === `match-update-${match.id}`) {
        try {
          const update = JSON.parse(e.newValue);
          if (update) {
            handleMatchUpdate({ detail: update });
          }
        } catch (error) {
          console.error('Failed to parse storage update:', error);
        }
      }
    };
    
    window.addEventListener('storage', handleStorageUpdate);

    return () => {
      window.removeEventListener('mrvl-match-updated', handleMatchUpdate);
      window.removeEventListener('storage', handleStorageUpdate);
    };
  }, [match.id]);

  // Use liveMatch instead of match for all display
  const displayMatch = liveMatch;
  // Debug log to check match data
  console.log('MatchCard - Match data:', {
    id: displayMatch.id,
    team1: displayMatch.team1,
    team2: displayMatch.team2,
    team1_name: displayMatch.team1?.name,
    team2_name: displayMatch.team2?.name,
    hasTeam1: !!displayMatch.team1,
    hasTeam2: !!displayMatch.team2
  });
  
  // Determine match winner based on series scores
  const series1 = displayMatch.series_score_team1 || displayMatch.team1_score || 0;
  const series2 = displayMatch.series_score_team2 || displayMatch.team2_score || 0;
  const isTeam1Winner = displayMatch.status === 'completed' && series1 > series2;
  const isTeam2Winner = displayMatch.status === 'completed' && series2 > series1;
  
  // Format time
  const formatTime = (dateString) => {
    if (!dateString) return 'TBD';
    const date = new Date(dateString);
    return date.toLocaleTimeString('en-US', { 
      hour: '2-digit', 
      minute: '2-digit',
      hour12: false 
    });
  };

  // Format date
  const formatDate = (dateString) => {
    if (!dateString) return '';
    const date = new Date(dateString);
    const today = new Date();
    const tomorrow = new Date(today);
    tomorrow.setDate(tomorrow.getDate() + 1);
    
    if (date.toDateString() === today.toDateString()) {
      return 'Today';
    } else if (date.toDateString() === tomorrow.toDateString()) {
      return 'Tomorrow';
    }
    
    return date.toLocaleDateString('en-US', { 
      month: 'short', 
      day: 'numeric' 
    });
  };

  // Get status display
  const getStatusDisplay = () => {
    switch (displayMatch.status) {
      case 'live':
        return (
          <div className="flex items-center justify-center">
            <span className="bg-red-600 text-white px-2 py-0.5 sm:py-1 text-[10px] sm:text-xs font-bold rounded-full flex items-center animate-pulse">
              <span className="w-1.5 h-1.5 sm:w-2 sm:h-2 bg-white rounded-full mr-1 sm:mr-1.5"></span>
              LIVE
            </span>
          </div>
        );
      case 'completed':
        return (
          <div className="text-center">
            <div className="text-lg sm:text-xl lg:text-2xl font-bold text-gray-900 dark:text-white">
              <span className={isTeam1Winner ? 'text-green-600 dark:text-green-400' : ''}>
                {series1}
              </span>
              <span className="mx-1 text-gray-400">-</span>
              <span className={isTeam2Winner ? 'text-green-600 dark:text-green-400' : ''}>
                {series2}
              </span>
            </div>
            <div className="text-[10px] sm:text-xs text-gray-500 dark:text-gray-500 mt-0.5">
              {displayMatch.format || 'BO3'}
            </div>
          </div>
        );
      case 'upcoming':
      case 'scheduled':
      default:
        return (
          <div className="text-center">
            <div className="text-sm sm:text-base lg:text-lg font-medium text-gray-900 dark:text-white">
              {formatTime(displayMatch.scheduled_at)}
            </div>
            <div className="text-[10px] sm:text-xs text-gray-500 dark:text-gray-500">
              {formatDate(displayMatch.scheduled_at)}
            </div>
          </div>
        );
    }
  };

  // VLR.gg-style match card
  if (isCompact) {
    // Compact version for sidebars
    return (
      <div 
        className="p-3 border border-gray-200 dark:border-gray-700 rounded hover:bg-gray-50 dark:hover:bg-gray-800 transition-colors cursor-pointer"
        onClick={() => navigateTo && navigateTo('match-detail', { id: match.id })}
      >
        <div className="flex items-center justify-between text-sm">
          <div className="flex-1">
            <div className="font-medium text-gray-900 dark:text-white">
              {displayMatch.team1?.short_name || displayMatch.team1?.name || 'TBD'}
            </div>
            <div className="font-medium text-gray-900 dark:text-white">
              {displayMatch.team2?.short_name || displayMatch.team2?.name || 'TBD'}
            </div>
          </div>
          <div className="text-right">
            {displayMatch.status === 'live' ? (
              <span className="text-red-600 font-bold text-sm animate-pulse">LIVE</span>
            ) : displayMatch.status === 'completed' ? (
              <div className="text-center">
                <div className="font-bold text-xl">
                  {displayMatch.team1_score || 0}-{displayMatch.team2_score || 0}
                </div>
                <div className="text-xs text-gray-500 dark:text-gray-400">
                  {displayMatch.format || 'BO3'}
                </div>
              </div>
            ) : (
              <div className="text-xs text-gray-500 dark:text-gray-500">
                {formatTime(displayMatch.scheduled_at)}
              </div>
            )}
          </div>
        </div>
      </div>
    );
  }

  // Full match card - Mobile optimized
  return (
    <div 
      className="card hover:shadow-lg transition-shadow cursor-pointer"
      onClick={() => navigateTo && navigateTo('match-detail', { id: match.id })}
    >
      <div className="p-3 sm:p-4 lg:p-5">
        {/* Event and format info - VLR.gg Style with Event Image */}
        <div className="flex items-center justify-between mb-3">
          <div className="flex items-center space-x-2">
            {/* Event Image - VLR.gg Style */}
            {displayMatch.event?.logo && (
              <div className="w-8 h-8 rounded overflow-hidden flex-shrink-0">
                <img 
                  src={getImageUrl(displayMatch.event.logo)} 
                  alt={displayMatch.event.name}
                  className="w-full h-full object-cover"
                />
              </div>
            )}
            {displayMatch.event?.name && (
              <span className="text-xs text-gray-500 dark:text-gray-500">
                {displayMatch.event.name}
              </span>
            )}
            {displayMatch.event?.tier && (
              <span className={`px-1.5 py-0.5 text-xs font-bold rounded border ${
                displayMatch.event.tier === 'S' ? 'bg-yellow-100 text-yellow-800 border-yellow-300 dark:bg-yellow-900/20 dark:text-yellow-200' :
                displayMatch.event.tier === 'A' ? 'bg-red-100 text-red-800 border-red-300 dark:bg-red-900/20 dark:text-red-200' :
                'bg-blue-100 text-blue-800 border-blue-300 dark:bg-blue-900/20 dark:text-blue-200'
              }`}>
                {displayMatch.event.tier}
              </span>
            )}
          </div>
          <span className="text-xs text-gray-500 dark:text-gray-500">
            {displayMatch.format || 'BO3'}
          </span>
        </div>

        {/* Teams and score - Mobile optimized layout */}
        <div className="flex items-center justify-between space-x-2 sm:space-x-3 lg:space-x-4">
          {/* Team 1 */}
          <div className={`flex-1 min-w-0 ${isTeam1Winner ? 'opacity-100' : displayMatch.status === 'completed' ? 'opacity-50' : ''}`}>
            <div className="flex items-center space-x-2 sm:space-x-3">
              <TeamLogo team={displayMatch.team1} size="w-8 h-8 sm:w-10 sm:h-10 lg:w-12 lg:h-12" className="flex-shrink-0" />
              <div className="min-w-0 flex-1">
                <div className="flex items-center space-x-1 sm:space-x-2">
                  <span 
                    className="font-semibold text-sm sm:text-base lg:text-lg text-gray-900 dark:text-white hover:text-blue-600 dark:hover:text-blue-400 cursor-pointer truncate"
                    onClick={(e) => {
                      e.stopPropagation();
                      navigateTo && navigateTo('team-detail', { id: displayMatch.team1?.id });
                    }}
                    title={displayMatch.team1?.name || 'TBD'}
                  >
                    {displayMatch.team1?.name || 'TBD'}
                  </span>
                  {displayMatch.team1?.country && (
                    <span className="text-sm flex-shrink-0">{getCountryFlag(displayMatch.team1.country)}</span>
                  )}
                </div>
                {displayMatch.team1?.short_name && (
                  <div className="text-xs text-gray-500 dark:text-gray-500 truncate">
                    {displayMatch.team1.short_name}
                  </div>
                )}
              </div>
            </div>
          </div>

          {/* Score/Status - Mobile optimized */}
          <div className="px-2 sm:px-3 lg:px-4 flex-shrink-0">
            {getStatusDisplay()}
          </div>

          {/* Team 2 */}
          <div className={`flex-1 min-w-0 ${isTeam2Winner ? 'opacity-100' : displayMatch.status === 'completed' ? 'opacity-50' : ''}`}>
            <div className="flex items-center justify-end space-x-2 sm:space-x-3">
              <div className="text-right min-w-0 flex-1">
                <div className="flex items-center justify-end space-x-1 sm:space-x-2">
                  {displayMatch.team2?.country && (
                    <span className="text-sm flex-shrink-0">{getCountryFlag(displayMatch.team2.country)}</span>
                  )}
                  <span 
                    className="font-semibold text-sm sm:text-base lg:text-lg text-gray-900 dark:text-white hover:text-blue-600 dark:hover:text-blue-400 cursor-pointer truncate"
                    onClick={(e) => {
                      e.stopPropagation();
                      navigateTo && navigateTo('team-detail', { id: displayMatch.team2?.id });
                    }}
                    title={displayMatch.team2?.name || 'TBD'}
                  >
                    {displayMatch.team2?.name || 'TBD'}
                  </span>
                </div>
                {displayMatch.team2?.short_name && (
                  <div className="text-xs text-gray-500 dark:text-gray-500 truncate">
                    {displayMatch.team2.short_name}
                  </div>
                )}
              </div>
              <TeamLogo team={displayMatch.team2} size="w-8 h-8 sm:w-10 sm:h-10 lg:w-12 lg:h-12" className="flex-shrink-0" />
            </div>
          </div>
        </div>

        {/* Additional info */}
        {displayMatch.status === 'live' && displayMatch.viewers && (
          <div className="mt-3 pt-3 border-t border-gray-200 dark:border-gray-700">
            <div className="flex items-center justify-center space-x-4 text-xs text-gray-500 dark:text-gray-500">
              <span>{displayMatch.viewers.toLocaleString()} viewers</span>
              {displayMatch.stream_url && (
                <a 
                  href={displayMatch.stream_url} 
                  target="_blank" 
                  rel="noopener noreferrer"
                  className="text-red-600 dark:text-red-400 hover:underline"
                  onClick={(e) => e.stopPropagation()}
                >
                  Watch Stream
                </a>
              )}
            </div>
          </div>
        )}

        {/* Map score for completed matches */}
        {displayMatch.status === 'completed' && displayMatch.maps_data && (() => {
          // Safely parse maps_data if it's a string
          let mapsArray = displayMatch.maps_data;
          if (typeof mapsArray === 'string') {
            try {
              mapsArray = JSON.parse(mapsArray);
            } catch (e) {
              console.error('Failed to parse maps_data:', e);
              return null;
            }
          }
          
          // Only render if we have a valid array
          if (!Array.isArray(mapsArray) || mapsArray.length === 0) {
            return null;
          }
          
          return (
            <div className="mt-3 pt-3 border-t border-gray-200 dark:border-gray-700">
              <div className="flex items-center justify-center space-x-2 text-xs">
                {mapsArray.map((map, index) => (
                  <div 
                    key={index} 
                    className={`px-2 py-1 rounded ${
                      map.winner_id === displayMatch.team1_id 
                        ? 'bg-blue-100 text-blue-800 dark:bg-blue-900/20 dark:text-blue-200' 
                        : 'bg-red-100 text-red-800 dark:bg-red-900/20 dark:text-red-200'
                    }`}
                  >
                    {map.map_name}: {map.team1_score || 0}-{map.team2_score || 0}
                  </div>
                ))}
              </div>
            </div>
          );
        })()}
      </div>
    </div>
  );
}

export default MatchCard;