import React, { useState, useEffect, useCallback } from 'react';
import { useAuth } from '../../hooks';
import { TeamLogo, getNewsFeaturedImageUrl, getImageUrl } from '../../utils/imageUtils';
import { formatTimeAgo } from '../../utils/dateUtils';
import MatchCard from '../../components/MatchCard';
import { MobileDashboard } from '../mobile/MobileEnhancements';
import MobileHomePage from './MobileHomePage';

function HomePage({ navigateTo }) {
  const { api } = useAuth();
  const [matches, setMatches] = useState([]);
  const [liveEvents, setLiveEvents] = useState([]);
  const [recentDiscussions, setRecentDiscussions] = useState([]);
  const [featuredNews, setFeaturedNews] = useState([]);
  const [isMobile, setIsMobile] = useState(window.innerWidth < 980);

  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth < 980);
    };
    
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  const fetchData = useCallback(async () => {
    try {
      // CRITICAL FIX: Use REAL BACKEND DATA ONLY - no more mock data
      console.log('HomePage: Fetching REAL LIVE DATA from backend...');
      
      let matchesData = [];
      let eventsData = [];
      let discussionsData = [];
      let newsData = [];

      try {
        // Get REAL matches from backend
        const matchResponse = await api.get('/matches');
        const rawMatches = matchResponse?.data?.data || matchResponse?.data || [];
        
        if (Array.isArray(rawMatches) && rawMatches.length > 0) {
          matchesData = rawMatches.map(match => ({
            id: match.id,
            team1: match.team1,
            team2: match.team2,
            event: {
              name: match.event_name || match.event?.name || 'Marvel Rivals Championship 2025',
              tier: match.event?.tier || 'S'
            },
            status: match.status || 'upcoming',
            format: match.format || 'BO3',
            time: match.scheduled_at 
              ? new Date(match.scheduled_at).toLocaleTimeString('en-US', { 
                  hour: '2-digit', 
                  minute: '2-digit' 
                })
              : match.status === 'live' ? 'LIVE' : 'TBD',
            team1_score: match.series_score_team1 || 0,
            team2_score: match.series_score_team2 || 0,
            score: `${match.series_score_team1 || 0}-${match.series_score_team2 || 0}`,
            //  FIXED: No more fake viewer generation - use real backend data only
            viewers: match.viewers || 0
          }));
          console.log('HomePage: Using REAL backend matches:', matchesData.length);
        }
      } catch (error) {
        console.error('HomePage: No backend matches available:', error);
        matchesData = []; // Empty if no backend data
      }

      try {
        // Get REAL events from backend
        const eventsResponse = await api.get('/events');
        const rawEvents = eventsResponse?.data?.data || eventsResponse?.data || [];
        
        if (Array.isArray(rawEvents) && rawEvents.length > 0) {
          eventsData = rawEvents.map(event => ({
            id: event.id,
            name: event.name,
            status: event.status,
            stage: event.stage || 'Main Event',
            prizePool: event.prize_pool || '$100,000',
            teams: event.teams_count || 16,
            region: event.region || 'International',
            //  NEW: Live Event Banner Data
            featured_image: event.featured_image,
            banner_image: event.banner_image
          }));
          console.log('HomePage: Using REAL backend events:', eventsData.length);
        }
      } catch (error) {
        console.error('HomePage: No backend events available:', error);
        eventsData = [];
      }

      // CRITICAL FIX: Get REAL forum discussions from backend ONLY  
      try {
        console.log('HomePage: Fetching REAL forum discussions from backend...');
        const forumsResponse = await api.get('/forums/threads');
        const rawDiscussions = forumsResponse?.data?.data || forumsResponse?.data || [];
        
        if (Array.isArray(rawDiscussions) && rawDiscussions.length > 0) {
          //  FIXED: Properly map backend forum data structure with null safety
          discussionsData = rawDiscussions
            .filter(thread => thread && thread.id && thread.title && thread.id > 0) // Only valid threads
            .slice(0, 8)
            .map(thread => {
              //  SAFE: Ensure all data has fallbacks to prevent NaN/undefined
              const author = thread.author?.name || thread.user_name || 'MRVL User';
              const replies = parseInt(thread.stats?.replies || thread.replies_count || 0) || 0;
              const dateForActivity = thread.meta?.last_reply_at || thread.meta?.created_at || thread.updated_at || thread.created_at;
              const category = thread.category?.name || thread.category_name || thread.category || 'general';
              
              return {
                id: thread.id,
                title: thread.title || 'Untitled Discussion',
                author: author,
                replies: replies,
                lastActivity: dateForActivity ? formatTimeAgo(dateForActivity) : 'Unknown',
                category: formatCategory(category)
              };
            });
          console.log('HomePage: Using REAL valid discussions:', discussionsData.length);
        }
      } catch (error) {
        console.error('HomePage: No backend discussions available:', error);
        discussionsData = [];
      }

      // Fetch REAL news from backend
      try {
        const newsResponse = await api.get('/news');
        const rawNews = newsResponse?.data?.data || newsResponse?.data || newsResponse || [];
        
        if (Array.isArray(rawNews) && rawNews.length > 0) {
          //  FIXED: Properly filter and map news data structure
          const featured = rawNews.filter(n => n.meta?.featured || n.featured).slice(0, 3);
          const selectedNews = featured.length > 0 ? featured : rawNews.slice(0, 3);
          
          //  FIXED: Map news data to expected frontend structure with null safety
          newsData = selectedNews
            .filter(article => article && article.id && article.title) // Only valid articles
            .map(article => {
              //  SAFE: Ensure all data has fallbacks to prevent NaN/undefined
              return {
                id: article.id,
                title: article.title || 'Untitled Article',
                slug: article.slug || `news-${article.id}`,
                excerpt: article.excerpt || '',
                content: article.content || '',
                featured_image: article.featured_image || null,
                image: article.featured_image || null, // Also provide as 'image' for compatibility
                author: article.author || { name: 'MRVL Team' },
                //  SAFE: Properly handle dates with fallbacks
                created_at: article.meta?.created_at || article.created_at || new Date().toISOString(),
                published_at: article.meta?.published_at || article.published_at || article.created_at,
                updated_at: article.meta?.updated_at || article.updated_at || article.created_at,
                featured: Boolean(article.meta?.featured || article.featured)
              };
            });
          
          console.log('HomePage: Using REAL backend news:', newsData.length);
        }
      } catch (error) {
        console.error('HomePage: No backend news available:', error);
        newsData = [];
      }
      
      // Set state with REAL backend data ONLY
      setMatches(matchesData);
      setLiveEvents(eventsData);
      setRecentDiscussions(discussionsData); // FIXED: Use real backend discussions
      setFeaturedNews(newsData);
      
      console.log('HomePage: All data loaded with REAL backend data ONLY');
    } catch (error) {
      console.error('HomePage: Critical error in fetchData:', error);
      
      // CRITICAL FIX: No more fallback to mock data
      setMatches([]);
      setLiveEvents([]);
      setFeaturedNews([]);
      setRecentDiscussions([]);
    }
  }, [api]);

  useEffect(() => {
    fetchData();
  }, [fetchData]);


  // Helper function to format category names
  const formatCategory = (category) => {
    const categoryMap = {
      'general': 'General',
      'tournaments': 'Tournaments',
      'hero-discussion': 'Heroes',
      'strategy': 'Strategy',
      'esports': 'Esports',
      'guides': 'Guides',
      'patch-notes': 'Updates',
      'bugs': 'Bugs',
      'feedback': 'Feedback',
      'team-recruitment': 'Recruitment',
      'meta-discussion': 'Meta'
    };
    return categoryMap[category] || 'Discussion';
  };

  // CRITICAL FIX: Ensure all navigation functions work properly with error handling
  const handleNavigationClick = (page, params = {}) => {
    console.log(`HomePage: Navigation clicked - ${page}`, params);
    
    if (!navigateTo || typeof navigateTo !== 'function') {
      console.error('HomePage: navigateTo function not available');
      alert('Navigation error: Please refresh the page and try again.');
      return;
    }
    
    try {
      navigateTo(page, params);
    } catch (error) {
      console.error('HomePage: Navigation error:', error);
      alert('Navigation failed. Please try again.');
    }
  };

  // CRITICAL FIX: Validate data before clicking handlers
  const handleMatchClick = (match) => {
    if (!match || !match.id) {
      console.error('HomePage: Invalid match data:', match);
      alert('Error: Match data is invalid. Cannot view match details.');
      return;
    }
    
    console.log('HomePage: Clicking match with ID:', match.id);
    handleNavigationClick('match-detail', { id: match.id });
  };

  const handleNewsClick = (article) => {
    if (!article || !article.id) {
      console.error('HomePage: Invalid article data:', article);
      alert('Error: Article data is invalid. Cannot view article.');
      return;
    }
    
    console.log('HomePage: Clicking news article with ID:', article.id, 'Title:', article.title);
    handleNavigationClick('news-detail', { id: article.id });
  };

  // CRITICAL FIX: Use REAL thread IDs from backend
  const handleDiscussionClick = (discussion) => {
    if (!discussion || !discussion.id) {
      console.error('HomePage: Invalid discussion data:', discussion);
      alert('Error: Discussion data is invalid. Cannot view thread.');
      return;
    }
    
    console.log('HomePage: Clicking discussion with REAL ID:', discussion.id, 'Title:', discussion.title);
    handleNavigationClick('thread-detail', { id: discussion.id });
  };

  const liveMatches = matches.filter(m => m.status === 'live');
  const upcomingMatches = matches.filter(m => m.status === 'upcoming');
  const completedMatches = matches.filter(m => m.status === 'completed').slice(0, 3); // Show last 3 completed matches
  const liveEventsBanner = liveEvents.filter(e => e.status === 'live');

  // Show mobile homepage on mobile devices
  if (isMobile) {
    return <MobileHomePage navigateTo={navigateTo} />;
  }

  return (
    <div className="max-w-7xl mx-auto px-3 sm:px-4 lg:px-6">
      {/* VLR.gg inspired responsive layout - Mobile optimized */}
      <div className="grid grid-cols-1 lg:grid-cols-12 gap-3 lg:gap-4">
        
        {/* Left Sidebar - Recent Discussions - Hidden on mobile */}
        <div className="hidden lg:block lg:col-span-3 order-2 lg:order-1">
          <div className="card">
            <div 
              className="px-3 py-2 border-b border-gray-200 dark:border-gray-600 cursor-pointer hover:bg-red-50 dark:hover:bg-red-900/10 transition-colors"
              onClick={() => handleNavigationClick('forums')}
            >
              <h2 className="text-xs sm:text-sm font-semibold text-red-600 dark:text-red-400 uppercase tracking-wide">
                Recent Discussion
              </h2>
            </div>
            <div className="divide-y divide-gray-200 dark:divide-gray-600">
              {recentDiscussions.length > 0 ? (
                recentDiscussions.map(discussion => (
                  <div 
                    key={discussion.id} 
                    className="recent-discussion-item cursor-pointer"
                    onClick={() => handleDiscussionClick(discussion)}
                  >
                    <h3 className="recent-discussion-title text-gray-900 dark:text-gray-100 hover:text-red-600 dark:hover:text-red-400 transition-colors">
                      {discussion.title}
                    </h3>
                    <div className="flex items-center justify-between recent-discussion-meta">
                      <span className="truncate text-xs text-gray-600 dark:text-gray-400">{discussion.author}</span>
                      <span className="text-xs text-gray-500 dark:text-gray-500">
                        {discussion.replies > 0 ? `${discussion.replies} replies` : 'No replies'}
                      </span>
                    </div>
                    <div className="text-xs text-gray-400 dark:text-gray-500 mt-1">
                      {discussion.lastActivity}
                    </div>
                  </div>
                ))
              ) : (
                <div className="p-4 text-center text-gray-500 dark:text-gray-500">
                  <div className="text-2xl mb-2"></div>
                  <div className="text-sm">No discussions yet</div>
                </div>
              )}
            </div>
          </div>
        </div>

        {/* Center Content - Live Banner + Featured News + Mobile Discussions */}
        <div className="lg:col-span-6 order-1 lg:order-2">
          {/*  MOVED: LIVE EVENT BANNER - Now in Center Column */}
          {liveEventsBanner.length > 0 && (
            <div className="mb-4">
              <div className="bg-gradient-to-r from-red-600 to-red-700 text-white rounded-lg overflow-hidden shadow-lg">
                {/* Event Background Image */}
                <div 
                  className="relative bg-cover bg-center"
                  style={{
                    backgroundImage: liveEventsBanner[0].featured_image 
                      ? `linear-gradient(rgba(220, 38, 38, 0.8), rgba(220, 38, 38, 0.8)), url('${getImageUrl(liveEventsBanner[0].featured_image)}')`
                      : 'linear-gradient(135deg, #dc2626, #b91c1c)'
                  }}
                >
                  <div className="p-3 sm:p-4">
                    <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-3">
                      <div className="flex items-center space-x-2 sm:space-x-3">
                        <div className="flex items-center space-x-1.5">
                          <div className="w-2.5 h-2.5 bg-white rounded-full animate-pulse"></div>
                          <span className="font-bold text-xs sm:text-sm">LIVE NOW</span>
                        </div>
                        <div>
                          <h3 className="font-bold text-base sm:text-lg">{liveEventsBanner[0].name}</h3>
                          <p className="text-xs sm:text-sm opacity-90">
                            {liveEventsBanner[0].stage}  {liveEventsBanner[0].prizePool}
                          </p>
                        </div>
                      </div>
                      
                      <button 
                        onClick={() => handleNavigationClick('events')}
                        className="px-3 py-1.5 sm:px-4 sm:py-2 bg-white text-red-600 font-medium rounded hover:bg-gray-100 transition-colors text-xs sm:text-sm whitespace-nowrap"
                      >
                        Watch Live 
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          )}

          {/* Featured News */}
          <div className="card">
            <div 
              className="px-3 py-2.5 sm:px-4 sm:py-3 border-b border-gray-200 dark:border-gray-600 cursor-pointer hover:bg-red-50 dark:hover:bg-red-900/10 transition-colors"
              onClick={() => handleNavigationClick('news')}
            >
              <div className="flex items-center justify-between">
                <h2 className="text-base sm:text-lg font-semibold text-red-600 dark:text-red-400">Featured News</h2>
                <button 
                  onClick={(e) => {
                    e.stopPropagation();
                    handleNavigationClick('news');
                  }}
                  className="text-xs sm:text-sm text-gray-600 dark:text-gray-400 hover:text-red-600 dark:hover:text-red-400 transition-colors"
                >
                  View all 
                </button>
              </div>
            </div>
            <div className="p-3 sm:p-4 space-y-3 sm:space-y-4">
              {featuredNews.length > 0 ? (
                featuredNews.map((article) => (
                  <div 
                    key={article.id}
                    className="flex space-x-2 sm:space-x-3 cursor-pointer group"
                    onClick={() => handleNewsClick(article)}
                  >
                    {article.image && (
                      <img 
                        src={getNewsFeaturedImageUrl(article)} 
                        alt={article.title}
                        className="w-16 h-12 sm:w-20 sm:h-16 object-cover rounded group-hover:opacity-80 transition-opacity flex-shrink-0"
                      />
                    )}
                    <div className="flex-1 min-w-0">
                      <h3 className="text-xs sm:text-sm font-medium text-gray-900 dark:text-white group-hover:text-red-600 dark:group-hover:text-red-400 transition-colors line-clamp-2">
                        {article.title}
                      </h3>
                      <p className="text-[10px] sm:text-xs text-gray-500 dark:text-gray-500 mt-0.5 sm:mt-1 line-clamp-1">
                        {article.excerpt || article.content?.substring(0, 100)}
                      </p>
                      <div className="flex items-center space-x-1.5 sm:space-x-2 mt-0.5 sm:mt-1 text-[10px] sm:text-xs text-gray-400 dark:text-gray-500">
                        <span className="text-gray-600 dark:text-gray-400">{article.author?.name || 'MRVL Team'}</span>
                        <span></span>
                        <span>{formatTimeAgo(article.created_at)}</span>
                      </div>
                    </div>
                  </div>
                ))
              ) : (
                <div className="text-center text-gray-500 dark:text-gray-500">
                  <div className="text-2xl mb-2"></div>
                  <div className="text-sm">No news articles yet</div>
                </div>
              )}
            </div>
          </div>

          {/* Mobile Matches Section - Show only on mobile */}
          <div className="lg:hidden mt-4 space-y-4">
            {/* Today's Matches - Mobile */}
            <div className="card">
              <div 
                className="px-3 py-2 border-b border-gray-200 dark:border-gray-600"
                onClick={() => handleNavigationClick('matches')}
              >
                <div className="flex items-center justify-between">
                  <h2 className="text-sm font-semibold text-red-600 dark:text-red-400">Today's Matches</h2>
                  <span className="text-xs text-gray-500 dark:text-gray-400">View all </span>
                </div>
              </div>
              <div className="divide-y divide-gray-200 dark:divide-gray-600">
                {[...liveMatches, ...upcomingMatches].slice(0, 5).map(match => (
                  <MatchCard key={match.id} match={match} navigateTo={navigateTo} isCompact={true} />
                ))}
                {liveMatches.length === 0 && upcomingMatches.length === 0 && (
                  <div className="p-4 text-center text-gray-500 dark:text-gray-400 text-sm">
                    No matches scheduled today
                  </div>
                )}
              </div>
            </div>

            {/* Recent Discussions - Mobile */}
            <div className="card">
              <div 
                className="px-3 py-2 border-b border-gray-200 dark:border-gray-600"
                onClick={() => handleNavigationClick('forums')}
              >
                <div className="flex items-center justify-between">
                  <h2 className="text-sm font-semibold text-red-600 dark:text-red-400">Recent Discussions</h2>
                  <span className="text-xs text-gray-500 dark:text-gray-400">View all </span>
                </div>
              </div>
              <div className="divide-y divide-gray-200 dark:divide-gray-600">
                {recentDiscussions.slice(0, 5).map(discussion => (
                  <div 
                    key={discussion.id}
                    className="p-3 hover:bg-gray-50 dark:hover:bg-gray-700/50 cursor-pointer transition-colors"
                    onClick={() => handleNavigationClick('thread-detail', { id: discussion.id })}
                  >
                    <h3 className="text-sm font-medium text-gray-900 dark:text-white line-clamp-2 mb-1">
                      {discussion.title}
                    </h3>
                    <div className="flex items-center justify-between text-xs text-gray-500 dark:text-gray-500">
                      <span>{discussion.author}</span>
                      <div className="flex items-center space-x-3">
                        <span>{discussion.replies} replies</span>
                        <span>{discussion.lastActivity}</span>
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          </div>
        </div>

        {/* Right Sidebar - Matches - Hidden on mobile */}
        <div className="hidden lg:block lg:col-span-3 order-3">
          <div className="space-y-3">
            {/* Live Matches */}
            {liveMatches.length > 0 && (
              <div className="card">
                <div 
                  className="px-3 py-2 border-b border-gray-200 dark:border-gray-600 cursor-pointer hover:bg-red-50 dark:hover:bg-red-900/10 transition-colors"
                  onClick={() => handleNavigationClick('matches')}
                >
                  <h2 className="text-xs sm:text-sm font-semibold text-red-600 dark:text-red-400 uppercase tracking-wide flex items-center">
                    <span className="w-2 h-2 bg-red-500 rounded-full mr-2 animate-pulse"></span>
                    Live Matches
                  </h2>
                </div>
                <div className="divide-y divide-gray-200 dark:divide-gray-600">
                  {liveMatches.filter(match => match && match.team1 && match.team2).map(match => (
                    <div 
                      key={match.id} 
                      className="p-3 hover:bg-gray-50 dark:hover:bg-gray-700/50 cursor-pointer transition-colors"
                      onClick={() => handleMatchClick(match)}
                    >
                      <div className="flex items-center justify-between mb-1">
                        <div className="flex items-center space-x-1">
                          <span className="text-xs font-medium text-red-600 dark:text-red-400">LIVE</span>
                          {/*  FIXED: Show real viewers or nothing if 0 */}
                          {match.viewers > 0 && (
                            <span className="text-xs text-gray-500 dark:text-gray-500">
                              {match.viewers.toLocaleString()} viewers
                            </span>
                          )}
                        </div>
                      </div>
                      <div className="text-center">
                        {/* BIGGER TEAM DISPLAYS */}
                        <div className="flex items-center justify-between text-sm font-medium text-gray-900 dark:text-white mb-1">
                          <div className={`flex items-center space-x-1 sm:space-x-2 ${match.status === 'completed' && match.team1_score <= match.team2_score ? 'opacity-50' : ''}`}>
                            <TeamLogo team={match.team1} size="w-5 h-5 sm:w-6 sm:h-6" />
                            <span className="match-team-name truncate text-gray-900 dark:text-gray-100 font-bold">{match.team1.short_name}</span>
                          </div>
                          <div className="text-center px-1 sm:px-2">
                            <div className="match-score-display text-red-600 dark:text-red-400 font-bold text-base sm:text-lg">
                              <span className={match.status === 'completed' && match.team1_score > match.team2_score ? 'text-green-600 dark:text-green-400' : ''}>
                                {match.team1_score}
                              </span>
                              -
                              <span className={match.status === 'completed' && match.team2_score > match.team1_score ? 'text-green-600 dark:text-green-400' : ''}>
                                {match.team2_score}
                              </span>
                            </div>
                            <div className="text-xs text-gray-500 dark:text-gray-500">
                              {match.format}
                            </div>
                          </div>
                          <div className={`flex items-center space-x-1 sm:space-x-2 ${match.status === 'completed' && match.team2_score <= match.team1_score ? 'opacity-50' : ''}`}>
                            <span className="match-team-name truncate text-gray-900 dark:text-gray-100 font-bold">{match.team2.short_name}</span>
                            <TeamLogo team={match.team2} size="w-5 h-5 sm:w-6 sm:h-6" />
                          </div>
                        </div>
                        {/* REAL TOURNAMENT NAME */}
                        <div className="text-xs text-gray-500 dark:text-gray-500 truncate font-medium">
                          {match.event?.name}
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            )}

            {/* Upcoming Matches */}
            <div className="card">
              <div 
                className="px-3 py-2 border-b border-gray-200 dark:border-gray-600 cursor-pointer hover:bg-red-50 dark:hover:bg-red-900/10 transition-colors"
                onClick={() => handleNavigationClick('matches')}
              >
                <h2 className="text-xs sm:text-sm font-semibold text-red-600 dark:text-red-400 uppercase tracking-wide">
                  Upcoming Matches
                </h2>
              </div>
              <div className="divide-y divide-gray-200 dark:divide-gray-600">
                {upcomingMatches.length > 0 ? (
                  upcomingMatches.filter(match => match && match.team1 && match.team2 && match.team1.name && match.team2.name).map(match => (
                    <div 
                      key={match.id} 
                      className="p-3 hover:bg-gray-50 dark:hover:bg-gray-700/50 cursor-pointer transition-colors"
                      onClick={() => handleMatchClick(match)}
                    >
                      <div className="text-xs text-gray-600 dark:text-gray-400 mb-1 font-medium">
                        {match.time}
                      </div>
                      <div className="text-center">
                        {/* BIGGER TEAM DISPLAYS */}
                        <div className="flex items-center justify-between text-sm font-medium text-gray-900 dark:text-white mb-1">
                          <div className="flex items-center space-x-1 sm:space-x-2">
                            <TeamLogo team={match.team1} size="w-5 h-5 sm:w-6 sm:h-6" />
                            <span className="match-team-name truncate text-gray-900 dark:text-gray-100 font-bold">{match.team1.short_name}</span>
                          </div>
                          <div className="text-center px-1 sm:px-2">
                            <div className="text-gray-400 dark:text-gray-500 font-bold text-sm sm:text-base">vs</div>
                            <div className="text-xs text-gray-500 dark:text-gray-500">
                              {match.format}
                            </div>
                          </div>
                          <div className="flex items-center space-x-1 sm:space-x-2">
                            <span className="match-team-name truncate text-gray-900 dark:text-gray-100 font-bold">{match.team2.short_name}</span>
                            <TeamLogo team={match.team2} size="w-5 h-5 sm:w-6 sm:h-6" />
                          </div>
                        </div>
                        {/* REAL TOURNAMENT NAME */}
                        <div className="text-xs text-gray-500 dark:text-gray-500 truncate font-medium">
                          {match.event?.name}
                        </div>
                      </div>
                    </div>
                  ))
                ) : (
                  <div className="p-4 text-center text-gray-500 dark:text-gray-500">
                    <div className="text-2xl mb-2"></div>
                    <div className="text-sm">No upcoming matches</div>
                  </div>
                )}
              </div>
            </div>

            {/* Recent Results */}
            {completedMatches.length > 0 && (
              <div className="card">
                <div 
                  className="px-3 py-2 border-b border-gray-200 dark:border-gray-600 cursor-pointer hover:bg-red-50 dark:hover:bg-red-900/10 transition-colors"
                  onClick={() => handleNavigationClick('matches')}
                >
                  <h2 className="text-xs sm:text-sm font-semibold text-red-600 dark:text-red-400 uppercase tracking-wide">
                    Recent Results
                  </h2>
                </div>
                <div className="divide-y divide-gray-200 dark:divide-gray-600">
                  {completedMatches.filter(match => match && match.team1 && match.team2).map(match => (
                    <div 
                      key={match.id} 
                      className="p-3 hover:bg-gray-50 dark:hover:bg-gray-700/50 cursor-pointer transition-colors"
                      onClick={() => handleMatchClick(match)}
                    >
                      <div className="text-xs text-gray-600 dark:text-gray-400 mb-1 font-medium">
                        Completed
                      </div>
                      <div className="text-center">
                        {/* Team displays with winner highlighting */}
                        <div className="flex items-center justify-between text-sm font-medium text-gray-900 dark:text-white mb-1">
                          <div className={`flex items-center space-x-2 ${match.team1_score <= match.team2_score ? 'opacity-60' : ''}`}>
                            <TeamLogo team={match.team1} size="w-6 h-6" />
                            <span className="truncate text-gray-900 dark:text-gray-100 font-bold">{match.team1.short_name}</span>
                          </div>
                          <div className="text-center px-1 sm:px-2">
                            <div className="match-score-display text-red-600 dark:text-red-400 font-bold text-base sm:text-lg">
                              <span className={match.team1_score > match.team2_score ? 'text-green-600 dark:text-green-400' : ''}>
                                {match.team1_score}
                              </span>
                              -
                              <span className={match.team2_score > match.team1_score ? 'text-green-600 dark:text-green-400' : ''}>
                                {match.team2_score}
                              </span>
                            </div>
                            <div className="text-xs text-gray-500 dark:text-gray-500">
                              {match.format}
                            </div>
                          </div>
                          <div className={`flex items-center space-x-2 ${match.team2_score <= match.team1_score ? 'opacity-60' : ''}`}>
                            <span className="truncate text-gray-900 dark:text-gray-100 font-bold">{match.team2.short_name}</span>
                            <TeamLogo team={match.team2} size="w-6 h-6" />
                          </div>
                        </div>
                        {/* Tournament info */}
                        <div className="text-xs text-gray-500 dark:text-gray-500">
                          {match.event.name}
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}

export default HomePage;